import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import WebContext from '../../../application/contexts/WebContext';

import CourseCard from '../Shared/CourseCard';
import ClientApis from '../APIs/ClientApis';

import Global from '../../../infrastructure/Common/Global';
import DataNotFound from '../../../infrastructure/Common/DataNotFound';

const Courses = () => {
    const { t } = useTranslation();
    const webCtx = useContext(WebContext);

    const [isLoading, setIsLoading] = useState(true);
    const [courses, setCourses] = useState([]);

    const baseURL = Global.baseUrl + "/";
    // const baseURL = 'https://it-be.mytextbook.net/';

    useEffect(() => {
        getCourses();
    }, []);

    const getCourses = () => {
        ClientApis.courses()
            .then(res => {
                setCourses(res.data);
                // setTimeout(() => {
                    setIsLoading(false);
                // }, 2000);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    return (
        <div className="container-xxl py-5">

            <div className="d-block d-md-flex align-items-end justify-content-between">
                <div className="section-header">
                    {/* <div className="primary">{t('menu_courses')}</div> */}
                    <h2 className="title fw-medium mb-0">{t('broad_selection_of_courses')}</h2>
                    <div>{t('broad_selection_of_courses_desc')}</div>
                </div>
            </div>

            <section>
                <div className="section-content mt-5">
                    <div className="row">
                        {isLoading ?
                            Array.from({ length: 3 }, (_, index) => (
                                <div className="col-lg-3 col-md-4 mb-4" key={index}>
                                    <div className="content-placeholder">
                                        <h5 className="w-100 min-h-200 placeholder-glow mt-2 me-3">
                                            <span className="placeholder col-12 min-h-200"></span>
                                        </h5>
                                        <p className="card-text placeholder-glow">
                                            <span className="placeholder col-7"></span>
                                            <span className="placeholder col-12 mt-3"></span>
                                            <span className="placeholder col-7 mt-2"></span>
                                            <br />
                                            <span className="placeholder col-4 my-3"></span>

                                            <span className="placeholder col-12 mt-3"></span>
                                            <span className="placeholder col-10"></span>
                                            <span className="placeholder col-8"></span>
                                        </p>
                                    </div>
                                </div>
                            ))
                            :
                            <>
                                {courses && courses.length ?
                                    <>
                                        {courses.map((item, index) => (
                                            <div className="col-lg-3 col-md-4 mb-4" key={index}>
                                            {/* <div className="col-lg-3 col-md-4 mb-4 mt-2" key={index} data-aos="fade-up" data-aos-easing="linear" data-aos-delay={index * 200}> */}
                                                <CourseCard course={item} appLang={webCtx.appLang} baseURL={baseURL} />
                                            </div>
                                        ))}
                                    </>

                                    : null}
                            </>
                        }
                        {!isLoading && !courses.length && <DataNotFound />}


                    </div>

                </div>
            </section>

        </div>
    );
};

export default Courses;